@import url(//fonts.googleapis.com/css2?family=Noto+Sans+HK&family=Ubuntu);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.infobox {
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
  min-width: 360px;
  height: auto;
  font-family: "Noto SansHK", "Ubuntu", serif;
}

.lrt__container {
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: auto;
          justify-content: auto;
}

.etaBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 10px;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-justify-content: left;
          justify-content: left;
}

.etaBox__row {
  padding-right: 5px;
}

.lrtTrain__length {
  width: 50px;
  padding-left: 5px;
}

.lrt__route {
  padding-right: 10px;
  width: 40px;
}

.lrt{
    padding-left: 20px;
    padding-right: 20px;
}

